import * as THREE from 'three';

const percentageDom = document.querySelector(".percentage")
const loadingDom = document.querySelector(".loading")

export const manager = new THREE.LoadingManager();
manager.onStart = function (url, itemsLoaded, itemsTotal) {
  // console.log('开始加载');
};

manager.onLoad = function () {
  // console.log('加载完成');
  percentageDom.innerHTML = `<div class="enterBtn">进入</div>`;
  const btn = document.querySelector(".enterBtn")
  btn.onclick = () => {
    loadingDom.remove()
    fullScreen()
  }
};

manager.onProgress = function (url, itemsLoaded, itemsTotal) {
  percentageDom.innerHTML = `耐心等会儿，已经加载 ${Math.floor(itemsLoaded / itemsTotal * 100)}% 了`
  // console.log('正在加载文件: ' + url + '\n已加载 ' + itemsLoaded + '，共 ' + itemsTotal);
};

manager.onError = function (url) {
  // console.error('There was an error loading ' + url);
};


function fullScreen() {
  if (isMobile() && !document.fullscreenElement) {
    document.documentElement.requestFullscreen();
  }
}

export function isMobile() {
  let userAgentInfo = navigator.userAgent;
  let Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
  let getArr = Agents.filter(i => userAgentInfo.includes(i));
  return getArr.length ? true : false;
}
